import { setDefaultWidgets } from 'jsoninput';
import React, { PropTypes } from 'react';
import TextField from 'material-ui/lib/text-field';

function StringField(props) {
    const errorMessage = props.errorMessage.length ?
        props.errorMessage :
        undefined;
    const onChange = function onChange(event) {
        props.onChange(event.target.value);
    };
    return (<TextField defaultValue={ props.value }
                       floatingLabelText={ props.schema.title || props.path[props.path.length - 1] }
                       errorText={ errorMessage }
                       onBlur={ onChange } />);
}

StringField.propTypes = {
    errorMessage: PropTypes.arrayOf(PropTypes.string),
    onChange: PropTypes.func.isRequired,
    value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    schema: PropTypes.shape({
        title: PropTypes.string
    }),
    path: PropTypes.arrayOf(PropTypes.string)
};
setDefaultWidgets({
    string: StringField,
    number: StringField
});



